[data-bgset]:before {
  content: "";
  display: block;
  speak: none;
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #F1E25B;
  transition: opacity .5s, z-index .5s ease .5s;
}

[data-bgset]:after {
  content: "";
  display: block;
  speak: none;
  z-index: 3;
  position: absolute;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
  transform: translateX(-50%) translateY(-50%);
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 5px solid #fff;
  border-top-color: transparent;
  opacity: 1;
  transition: opacity .5s, z-index .5s ease .5s;
  animation: loading .5s linear infinite;
}

[data-bgset].lazyload {
